
import { CheckCircle, XCircle } from 'lucide-react';
import { Badge } from '@/components/ui/badge';
import type { Candidate, Submission, Test } from '@/lib/types';
import { format } from 'date-fns';

type SectionScore = {
  name: string;
  scored: number;
  total: number;
};

interface ReportTemplateProps {
  submission: Submission;
  test: Test;
  candidate: Candidate;
  sectionScores: SectionScore[];
}

export function ReportTemplate({ submission, test, candidate, sectionScores }: ReportTemplateProps) {
  const isShortlisted = submission.result === 'pass';
  const totalMarks = test.questions.reduce((sum, q) => sum + (q.marks || 0), 0);

  return (
    <div id="pdf-content" className="p-10 bg-white text-black font-sans w-[800px]">
      <header className="text-center mb-8 border-b-2 border-gray-200 pb-4">
        <h1 className={`text-3xl font-bold ${isShortlisted ? 'text-green-600' : 'text-red-600'}`}>
          {isShortlisted ? '‚úÖ Shortlisted' : '‚ùå Not Shortlisted'}
        </h1>
        <p className="text-lg text-gray-600">TechSavvy Solutions</p>
      </header>

      <main>
        <div className="grid grid-cols-3 gap-4 mb-8 text-sm">
          <div>
            <p className="font-semibold text-gray-500">CANDIDATE NAME</p>
            <p className="text-lg">{candidate.name}</p>
          </div>
          <div>
            <p className="font-semibold text-gray-500">TEST DATE</p>
            <p className="text-lg">{format(new Date(submission.submittedAt), 'dd/MM/yyyy')}</p>
          </div>
          <div>
            <p className="font-semibold text-gray-500">JOB ROLE</p>
            <p className="text-lg">{test.title}</p>
          </div>
        </div>

        <div className="bg-gray-50 p-6 rounded-lg text-center mb-8">
          <p className="font-semibold text-gray-500">FINAL RESULT</p>
          <div className="flex items-center justify-center gap-2 mt-2">
            {isShortlisted ? (
              <CheckCircle className="h-8 w-8 text-green-500" />
            ) : (
              <XCircle className="h-8 w-8 text-red-500" />
            )}
            <p className="text-2xl font-bold">{isShortlisted ? 'üéâ Shortlisted for Next Round' : 'Not Shortlisted'}</p>
          </div>
          <div className="mt-2">
            <Badge variant={isShortlisted ? 'secondary' : 'destructive'}>
              Score: {submission.score} / {totalMarks}
            </Badge>
          </div>
        </div>

        <div>
          <h2 className="text-xl font-semibold mb-4 text-gray-700">Section Scores</h2>
          <div className="space-y-4">
            {sectionScores.map((section, index) => (
              <div key={index} className="p-4 border rounded-lg">
                <div className="flex justify-between items-center">
                  <p className="capitalize font-medium">{section.name}</p>
                  <p className="font-semibold">{section.scored} / {section.total}</p>
                </div>
                <div className="w-full bg-gray-200 rounded-full h-2.5 mt-2">
                  <div
                    className={`h-2.5 rounded-full ${section.scored / section.total >= 0.5 ? 'bg-green-500' : 'bg-red-500'}`}
                    style={{ width: `${(section.scored / section.total) * 100}%` }}
                  ></div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </main>

      <footer className="text-center mt-10 text-gray-500 text-sm">
        {isShortlisted ? (
          <p>üëâ Next Round details will be shared via email.</p>
        ) : (
          <p>üëâ Thank you for participating. Better luck next time!</p>
        )}
      </footer>
    </div>
  );
}
